/*Configuración de aplicación # 2: listado de logros académicos*/

#contenedor-lista{
    width: 60%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #192231;
    border-radius: 5px;
    padding: 30px 20px;
}

#icono-lista{
    font-size: 90px;
    color: #fff;
}

#carrera{
    width: 80%;
    font-size: 40px;
    font-family: 'Didact Gothic', sans-serif;
    color: #fff;
    text-align: center;
    padding-bottom: 20px;
}

#establecimiento{
    width: 80%;
    font-size: 30px;
    font-family: 'Didact Gothic', sans-serif;
    color: #fff;
    text-align: center;
    padding-bottom: 20px;   
}

#boton-lista{
    width: 25%;
    border: none;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #494E6B;
    color: #fff;
}

#invisible{
    display: none;
}

/*Configuración de aplicación # 4: listado de reconocimientos profesionales y laborales*/

.contenedor-logros{
    width: 100%;
    display: flex;
    flex-direction: row;
}

.item-logro{
    font-family: 'Didact Gothic', sans-serif;
    font-size: 20px;
    color: #000;
}

.item-institucion{
    font-family: 'Didact Gothic', sans-serif;
    font-size: 18px;
    font-weight: bolder;
    padding-bottom: 20px;
    color: #000;
}

.titulo-laboral, .titulo-academico{
    font-family: 'Didact Gothic', sans-serif;
    font-size: 25px;
    padding-bottom: 20px;
    color: #000;
}

.contenedor-boton-logro{
    display: none;
}

.boton-laboral, .boton-academia{
    width: 30%;
}

.logro-academico{
    width: 45%;
}

.logro-laboral{
    width: 55%;
    border-left: 2px solid #000 ;
    padding-left: 20px;
}

